@keyframes animSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 100%;
  max-height: 100%;
  overflow: scroll;
}

.collapseBtn {
  position: absolute;
  pointer-events: all;
  top: 246px;
  left: 0px;
  width: 24px;
  height: 96px;
  cursor: pointer;
}

.modal {
  margin: 64px;
  background-color: black;
  border-radius: 16px;
  padding: 16px;
  pointer-events: all;
}

.modalInvisible {
  visibility: hidden;
}

.closeModal {
  border-radius: 5px;
  background-color: #222;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 5px;
  font-weight: bolder;
  font-size: 20px;
}

.closeModal:hover {
  background-color: white;
  color: #222;
}

.header {
  position: relative;
  display: flex;
  flex-direction: column;
}

.header h1 {
  margin: 0;
}

.taskInput {
  display: flex;
  align-items: stretch;
  margin: 5px;
  margin-top: 32px;
}

.taskInput input {
  border: 0;
  background-color: #222;
  border-radius: 5px;
  font-size: 30px;
  color: white;
  padding: 10px;
  flex-grow: 1;
}

.taskInputAddButton {
  border-radius: 5px;
  background-color: #222;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: bolder;
  font-size: 20px;
  margin-left: 10px;
  width: 64px;
}

.taskInputAddButton:hover {
  background-color: white;
  color: #222;
}

.singleItemContainer {
  display: flex;
}

.deleteTaskBtn {
  border-radius: 5px;
  background-color: #222;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: bolder;
  font-size: 20px;
  margin-left: 10px;
  width: 64px;
  margin: 5px;
}

.deleteTaskBtn:hover {
  background-color: white;
  color: #222;
}

.item {
  flex-grow: 1;
  text-align: start;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
}

.item:hover {
  background-color: #222;
}

.itemOpen {
  color: white;
}

.itemDone {
  color: #666;
}

.indicator {
  width: 24px;
  height: 24px;
  margin: 4px;
  margin-right: 16px;
}

.indicatorSpinning {
  animation-name: animSpin;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
