@keyframes animSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes animShake {
  0% {
    transform: translate(1px, 1px);
  }
  10% {
    transform: translate(-1px, -2px);
  }
  20% {
    transform: translate(-3px, 0px);
  }
  30% {
    transform: translate(3px, 2px);
  }
  40% {
    transform: translate(1px, -1px);
  }
  50% {
    transform: translate(-1px, 2px);
  }
  60% {
    transform: translate(-3px, 1px);
  }
  70% {
    transform: translate(3px, 1px);
  }
  80% {
    transform: translate(-1px, -1px);
  }
  90% {
    transform: translate(1px, 2px);
  }
  100% {
    transform: translate(1px, -2px);
  }
}

@keyframes animFall {
  0% {
    transform: translateY(0) scale(100%);
  }
  100% {
    transform: translateY(200%) scale(300%);
  }
}

@keyframes animExplosion {
  0% {
    transform: translateY(200%) scale(300%);
    opacity: 1;
  }
  50% {
    transform: translateY(200%) scale(700%);
    opacity: 1;
  }
  100% {
    transform: translateY(200%) scale(1000%);
    opacity: 0;
  }
}

@keyframes animNewMoon {
  0% {
    transform: translateY(-200%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.moon {
  pointer-events: all;
  animation-name: animSpin;
  animation-duration: 40s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.moonShake {
  animation: animShake 250ms;
}

.moonFall {
  animation: animFall 5s linear;
}

.explosion {
  animation: animExplosion 5s linear;
}

.spawnMoon {
  animation: animNewMoon 5s ease-out;
}
