@keyframes sparkle {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.sky {
  width: 100%;
  height: 100%;
  position: relative;
}

.star {
  position: absolute;
  animation-name: sparkle;
  animation-iteration-count: infinite;
  pointer-events: all;
}

.clickableStar img {
  cursor: pointer;
  width: 35px;
  height: 35px;
}
