.root {
  z-index: 0;

  display: grid;
  background-color: #2e2642;
  font-size: 30px;
  color: white;
  text-align: center;
  grid-template-columns: 20% auto 20%;
  grid-template-rows: 10% auto auto auto 10%;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}

.stars {
  z-index: 1;

  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 4;
}

.moon {
  z-index: 2;

  grid-column: 2;
  grid-row: 2;
}

.hill {
  z-index: 3;

  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 6;
  min-width: 100vw;
}

.text {
  z-index: 4;

  grid-column: 2;
  grid-row: 4;
}

.music {
  z-index: 5;

  grid-column-start: 1;
  grid-row-start: 1;
  grid-row-end: 6;

  width: 0;
  overflow: visible;
}

.image {
  z-index: 6;

  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 6;
  max-height: 100vh;
}

.todo {
  z-index: 6;

  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 6;
  max-height: 100vh;
}
