.container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  justify-items: center;
  justify-content: center;
  pointer-events: all;
}

.background {
  z-index: 100;
  grid-row: 1;
  grid-column: 1;
  background-position: center;
  background-size: cover;
  filter: blur(15px);
  width: 100%;
  height: 100%;
}

.image {
  z-index: 101;
  grid-row: 1;
  grid-column: 1;
  max-width: 80%;
  max-height: 80%;
  margin: auto;
}

.invisible {
  position: relative;
}

.collapseButton {
  position: absolute;
  pointer-events: all;
  left: 0;
  top: 128px;
  width: 24px;
  height: 96px;
  cursor: pointer;
}
