.root {
  position: absolute;
  height: 100%;
  width: 0;
  overflow: visible;
  pointer-events: none;
}

.container {
  width: 424px;
  position: relative;
  display: flex;
  flex-direction: row;
  height: 100%;
  justify-content: flex-start;
  align-items: flex-start;
  transition: all 250ms ease;
}

.containerCollapse {
  left: -400px;
}

.containerExpand {
  left: 0;
}

.player {
  box-shadow: 10px 0 10px #00000055;
  width: 400px;
  height: 100%;
  pointer-events: all;
}

.collapseButton {
  margin-top: 16px;
  cursor: pointer;
  width: 24px;
  height: 96px;
  pointer-events: all;
}
